import React from "react";

export default class Header extends React.Component {

    handleSubNavClick = () => {
        event.stopPropagation();

        const { name: catalogFilter } = event.target;
        this.props.onSubNavClick(catalogFilter);
    }

    render() {
        return (
            <div className="header">
                <div className="container">
                    <div className="logo">
                        <a href="/">test<b>myskill</b></a>
                    </div>
                    <div className="subnavcontain">
                        <div className="container">
                            <a href="/" name="none">All</a>
                            <a href="/english" name="english">English</a>
                            <a href="/tech" name="tech">Technology</a>
                        </div>
                    </div>
                </div>


            </div>
        );
    }
}



