import React from "react";

export default class CatalogItem extends React.Component {

    handleClick = (event) => {
        const { comingSoon } = this.props;
        if (comingSoon === true) {
            event.stopPropagation();
            event.preventDefault();
            alert("Oops, we are still working on this item.");
        }
    }

    render() {
        const { name, link, className } = this.props;
        return (
            <a href={link} onClick={this.handleClick}>
                <div className={`item ${className || ""}`}>
                    <h1>{name}</h1>
                </div>
            </a>
        );
    }
}



