import React from "react";
import CatlogItem from "./CatlogItem";
import "./catalog.css"

export default class Catalog extends React.Component {

    renderTechnology() {
        const { filter } = this.props;
        if (filter === "none" || filter === "tech") {
            return (
                <div>
                    <h1 className="section-heading">Technology<span className="arrow"></span></h1>
                    <br />
                    <CatlogItem name="JavaScript" link="/tech/javascript" />
                    <CatlogItem className="coming-soon" name="CSS" link="/tech/css" comingSoon={true} />
                    <CatlogItem className="coming-soon" name="React" link="/tech/react" comingSoon={true} />

                </div>
            );
        }

    }

    renderEnglish() {
        const { filter } = this.props;
        if (filter === "none" || filter === "english") {
            return (
                <div>
                    <h1 className="section-heading">English<span className="arrow"></span></h1>
                    <br />
                    <CatlogItem name="Words" link="/english/words" />
                    <CatlogItem className="coming-soon" name="Grammer" link="/english/words" comingSoon={true} />
                </div>
            );
        }
    }

    render() {

        return (
            <div className="items">
                {this.renderEnglish()}
                {this.renderTechnology()}
            </div>
        );
    }
}



