import "./normalize.css";
import "./app.css";
import "./app.mobile.css";

import React from "react";

import Catalog from "../Catalog";
import Header from "./Header";
import ObjectiveQuestions from "../ObjectiveQuestions";
import { doGet } from "../../helpers/xhr";
import { getSetQuestions } from "../../helpers/questionsHelper";

const CATAGORY_ROUTES = ["/tech", "/english"];
const PAGE_ROUTES = ["/tech/javascript", "/english/words"];

class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            catalogFilter: "none"
        };
    }

    componentDidMount() {
        const { pathname: pathName } = window.location;
        const type = pathName.substring(pathName.lastIndexOf("/") + 1);
        if (PAGE_ROUTES.indexOf(pathName) >= 0) {
            doGet(`/data/${type}.json`).then((nodes) => {
                const setQuestions = getSetQuestions(nodes);
                this.setState({ nodes, setQuestions, type });
            });
            return;
        }

        if (CATAGORY_ROUTES.indexOf(pathName) >= 0) {
            this.setState({ catalogFilter: type });
        }
    }

    renderQuestionsComponent() {
        const { type, setQuestions, catalogFilter } = this.state;

        if (type) {
            return (<ObjectiveQuestions type={type} questions={setQuestions} />);
        } else {
            return (<Catalog filter={catalogFilter} />);
        }
    }

    handleSubNavClick = (catalogFilter) => {
        this.setState({ catalogFilter });
    }

    render() {
        return (
            <div className="app">
                <Header onSubNavClick={this.props.handleSubNavClick} />
                <div className="app-container">
                    {this.renderQuestionsComponent()}
                </div>
            </div>
        );
    }
}

export default App;



